import React, { Component } from 'react';
export default class Topics extends Component{
    constructor(props){
        super(props)
        console.log("props");

        this.state = {
            inputValue: '',
        };
    }

  handleChange = (ev) => {
      this.setState({inputValue: ev.target.value.toUpperCase()});
      console.log('ev', this.state.inputValue);
  }
  submitTodoItem = () =>{
     
  }

    render(){
        return(
            <div className={"topics"}>
                <form>
                <label>
                    <input name="{text}" type="text" name="name" value={this.state.inputValue} onChange={this.handleChange}/>
                </label>

                    <button className={"topics-button"} onClick={this.submitTodoItem}>submit</button>
                </form>
            </div>
        );
    }
}